.parallelogram {
	width: 200px;
	height: 100px;
	transform: skew(140deg);
	background: #555;
}

.contaniners {
    position: relative;
    flex-grow: 1;
    margin: auto;
    max-width: 80rem;
    max-height: 60rem;
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: repeat(4, 1fr);
    grid-gap: 2vmin;
    justify-items: center;
    align-items: center;
  }

  .profile {
    position: center;
    flex-grow: 0;
    margin: auto;
    max-width: 50rem;
    max-height: 40rem;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(1, 1fr);
    justify-items: center;
    align-items: center;
  }
   
  .imgs {
    z-index: 1;
    grid-column: span 2;
    max-width: 100%;
    margin-bottom: -52%;
    -webkit-clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
            clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
    -webkit-transform: scale(1);
            transform: scale(1);
    transition: all .3s;
  }
  .imgs:nth-child(7n + 1) {
    grid-column: 2 / span 2;
  }
  .imgs:hover {
    z-index: 2;
    -webkit-transform: scale(2);
            transform: scale(2);
  }

  .tooltip {
    position: relative;
    display: inline-block;
    border-bottom: 1px dotted black;
  }
  
  .tooltip .tooltiptext {
    visibility: hidden;
    width: 120px;
    background-color: black;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
  
    /* Position the tooltip */
    position: absolute;
    z-index: 1;
  }
  
  .tooltip:hover .tooltiptext {
    visibility: visible;
  }

  .imgss {
    z-index: 1;
    grid-column: span 1;
    max-width: 100%;
    margin-bottom: -52%;
    -webkit-clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
            clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
    -webkit-transform: scale(1);
            transform: scale(1);
    transition: all .3s;
  }
  .imgss:nth-child(7n + 1) {
    grid-column: 1 / span 1;
  }
  .imgss:hover {
    z-index: 2;
    -webkit-transform: scale(2);
            transform: scale(2);
  }

